.page-card-head {
  padding: max(5vh, 30px) 0px 14px 0;
  margin: 0 auto;
  text-align: center;
  font-size: var(--text-xl);
  font-weight: 600;
}
.page-card-head img {
  max-height: 96px;
}
.page-card-head h4 {
  margin-top: 1rem;
  font-size: var(--text-xl);
  font-weight: var(--weight-semibold);
  letter-spacing: 0.01em;
  color: var(--text-color);
}